.wiersz_mobile {
    width: 90%;
    display: flex;
    flex-direction: column;
    height: 8vh;
    margin-bottom: 3vh;

    &.textarea {
        height: 20vh;
    }

    input {
        width: 100%;
        height: 100%;
        border: 1px solid rgba(0, 0, 0, 0.2);
    }

    label {
        font-size: calc(1vh + 7px);
    }
}